
.bigDiv {
  overflow: hidden;
  width: 100%;
}
.bigDiv .heroesDiv {
  width: 7.21rem;
  overflow: hidden;
  float: left;
  margin-left: .29rem;
}
.bigDiv .heroesDiv .left_hero {
  width: 3.6rem;
  height: 1.15rem;
  float: left;
  position: relative;
  margin-top: .32rem;
  margin-bottom: .03rem;
}
.bigDiv .heroesDiv .left_hero img {
  width: 1.15rem;
  height: 1.15rem;
  position: absolute;
  left: 0;
  border-radius: 50%;
}
.bigDiv .heroesDiv .left_hero .chinaName {
  font-size: .28rem;
  font-weight: bold;
  position: absolute;
  top: .23rem;
  left: 1.29rem;
  color: #000000;
  width: 1.15rem;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.bigDiv .heroesDiv .left_hero .englishName {
  font-size: .26rem;
  font-weight: normal;
  position: absolute;
  top: .64rem;
  left: 1.29rem;
  color: #000000;
  width: 2.2rem;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}



