
.topSpace {
  width: 100%;
  height: .15rem;
}
.videoDIv {
  width: 3.31rem;
  min-height: 3.6rem;
  float: left;
  margin-left: .295rem;
  position: relative;
}
.videoDIv img {
  width: 3.31rem;
  height: 2.19rem;
  position: absolute;
  top: 0;
  left: 0;
}
.videoDIv .videoTitle {
  position: absolute;
  color: #323232;
  top: 2.36rem;
  left: 0;
  width: 3.31rem;
  font-size: .23rem;
  font-weight: bold;
}
.videoDIv .videoData {
  color: #828282;
  font-size: .18rem;
  position: absolute;
  top: 3.1rem;
  left: 0;
  text-overflow: ellipsis;
  width: 2rem;
  white-space: nowrap;
  overflow: hidden;
}
.videoDIv .pinglun {
  color: #3fa8f9;
  margin-left: 2.4rem;
  position: absolute;
  top: 3.1rem;
  right: .05rem;
  font-size: .18rem;
  text-overflow: ellipsis;
  width: 1rem;
  white-space: nowrap;
  overflow: hidden;
}



