
.detailtitle {
  width: 100%;
  height: .8rem;
  background-color: #f0f0f0;
  position: relative;
}
.detailtitle span {
  height: .8rem;
  display: block;
  float: left;
  color: #3b3b3b;
  font-size: .26rem;
  font-weight: bold;
  margin-left: .16rem;
  padding-left: .2rem;
  line-height: .8rem;
}
.victorydiv {
  width: 100%;
  height: .93rem;
  border-bottom: .01rem solid #e1e1e1;
  position: relative;
}
.victorydiv .vdiv {
  position: absolute;
  top: .18rem;
  left: .18rem;
  width: 1.44rem;
  height: .55rem;
  background-color: #ff9900;
}
.victorydiv .vdiv p {
  line-height: .55rem;
  font-size: .27rem;
  color: #fffefd;
  font-weight: bold;
  text-align: center;
}
.victorydiv img {
  width: 1.59rem;
  height: .39rem;
  position: absolute;
  top: .29rem;
  right: .33rem;
}
.victory {
  width: 100%;
  height: 1.9rem;
  position: relative;
  overflow: hidden;
}
.victory .touxiang {
  width: 1.25rem;
  height: 1.26rem;
  background-image: url("../images/img/10.jpg");
  background-repeat: no-repeat;
  background-size: 100%;
  position: absolute;
  top: .09rem;
  left: .19rem;
}
.victory .touxiang img {
  position: absolute;
  width: 1.25rem;
  height: 1.26rem;
  top: 0;
  left: 0;
}
.victory .vname {
  color: #010101;
  font-size: .28rem;
  font-weight: bold;
  margin-left: 1.76rem;
  margin-top: .26rem;
}
.victory .grade {
  position: absolute;
  width: 1.25rem;
  height: .55rem;
  top: 1.35rem;
  left: .19rem;
  border: .01rem solid #24a723;
  box-sizing: border-box;
}
.victory .grade p {
  color: #000000;
  font-size: .24rem;
  font-weight: bold;
  text-align: center;
  line-height: .55rem;
}
.victory .equipdiv {
  margin-top: .48rem;
  margin-left: 1.61rem;
  position: relative;
  width: 4.08rem;
  height: .5rem;
  overflow: hidden;
}
.victory .equipdiv div {
  position: relative;
  width: .5rem;
  height: .5rem;
  margin-left: .18rem;
  padding-bottom: .56rem;
  background-image: url("../images/img/1.png");
  background-repeat: no-repeat;
  background-size: 100%;
  float: left;
}
.victory .equipdiv div img {
  width: .5rem;
  height: .5rem;
  position: absolute;
  left: 0;
  top: 0;
}
.victory .xiahuaxian {
  width: 5.82rem;
  height: .01rem;
  position: absolute;
  background-color: #e1e1e1;
  bottom: 0;
  right: 0;
}
.victory .zhanji {
  position: absolute;
  right: .36rem;
  top: .83rem;
  font-size: .2rem;
  color: #a4a4a4;
}
.fenge {
  width: 100%;
  height: .02rem;
  background-color: #e1e1e1;
}



