
.headers {
  width: 100%;
  height: .8rem;
  background-image: url("../images/img/header.png");
  position: fixed;
  top: 0;
  z-index: 999;
}
.headers .return {
  position: absolute;
  width: .18rem;
  height: .34rem;
  left: .33rem;
  top: .26rem;
  background-image: url("../images/img/u31.png");
  background-size: cover;
  background-repeat: no-repeat;
}
.headers .header_title {
  color: #ffffff;
  font-size: .34rem;
  text-align: center;
  line-height: .8rem;
}
.info {
  width: 100%;
  height: 3.56rem;
  background-image: url("../images/img/Sumdetails-1.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  text-align: center;
  position: relative;
}
.info img {
  width: 1.28rem;
  height: 1.28rem;
  position: absolute;
  left: 3.11rem;
  top: .47rem;
  border-radius: 50%;
}
.info .dengji {
  font-weight: bold;
  font-size: .29rem;
  color: #fffeff;
  width: .65rem;
  position: absolute;
  left: 3.4rem;
  top: 1.93rem;
}
.info .pername {
  position: absolute;
  right: 3.73rem;
  top: 2.54rem;
  color: #fffeff;
  font-size: .23rem;
}
.info .duanwei {
  position: absolute;
  left: 4.07rem;
  top: 2.54rem;
  color: #fffeff;
  font-size: .23rem;
}
.info .zhanli {
  position: absolute;
  right: 3.57rem;
  top: 2.95rem;
  color: #fffeff;
  font-size: .23rem;
}
.info .zan {
  position: absolute;
  left: 4.09rem;
  top: 2.95rem;
  color: #fffeff;
  font-size: .23rem;
}
.record {
  width: 100%;
  height: 3.82rem;
  border-bottom: 1px solid #e1e1e1;
}
.record ul li {
  list-style: none;
}
.record ul li div {
  float: left;
  height: 1.91rem;
  width: 2.5rem;
  position: relative;
}
.record ul li div img {
  width: .66rem;
  height: .66rem;
  position: absolute;
  top: .44rem;
  left: 1rem;
}
.record ul li div p {
  position: absolute;
  top: 1.39rem;
  left: .7rem;
  font-size: .22rem;
  color: #333333;
}
.matches {
  overflow-y: auto;
  height: 12.56rem;
}
.enterinfo {
  width: 100%;
  height: 1.51rem;
  position: relative;
}
.enterinfo .imginfo {
  width: 1rem;
  height: 1rem;
  position: absolute;
  border-radius: 50%;
  top: .33rem;
  left: .41rem;
}
.enterinfo .infoname {
  position: absolute;
  top: .43rem;
  left: 1.76rem;
  font-size: .29rem;
  font-weight: bold;
  color: #fe0000;
}
.enterinfo .typegame {
  position: absolute;
  top: .97rem;
  left: 1.76rem;
  font-size: .23rem;
  font-weight: bold;
  color: #666666;
}
.enterinfo .enimg {
  position: absolute;
  top: .72rem;
  right: .41rem;
  width: .13rem;
  height: .23rem;
}
.enterinfo .xiahuaxian {
  width: 7rem;
  height: .02rem;
  position: absolute;
  background-color: #e1e1e1;
  bottom: 0;
  right: 0;
}
.loading {
  width: 100%;
  height: .5rem;
  display: block;
  font-size: .28rem;
  color: #000000;
  text-align: center;
  line-height: .5rem;
}



