
.swiper-pagination span {
  width: .14rem;
  height: .14rem;
  border-radius: 50%;
  opacity: 1;
}
.swiper-pagination .swiper-pagination-bullet {
  background-color: #d9d9d9;
}
.swiper-pagination .swiper-pagination-bullet-active {
  background-color: #41a5fb;
}
.newsDiv {
  width: 100%;
  height: 1.5rem;
  border-bottom: .02rem solid #f0f0f0;
  position: relative;
}
.newsDiv img {
  width: 1.5rem;
  height: 1.3rem;
  position: absolute;
  left: .11rem;
  top: .1rem;
}
.newsDiv .lineOne {
  color: #323232;
  font-size: .28rem;
  position: absolute;
  left: 1.82rem;
  top: .14rem;
  font-weight: bold;
}
.newsDiv .lineTwo {
  width: 5.6rem;
  color: #323232;
  font-size: .25rem;
  position: absolute;
  left: 1.82rem;
  top: .61rem;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.newsDiv .lineThree {
  color: #323232;
  font-size: .23rem;
  position: absolute;
  left: 1.82rem;
  top: 1.03rem;
}
.newsDiv .lineThree .lineX {
  color: #41a7fc;
  margin-left: .51rem;
}



