
.search {
  width: 100%;
  height: 1.27rem;
  background-color: #40a6fb;
  position: relative;
  margin-top: -0.01rem;
}
.search input {
  width: 6.15rem;
  height: .69rem;
  position: absolute;
  left: .45rem;
  top: .4rem;
  font-size: .28rem;
  color: #787878;
  border-radius: .04rem;
  padding-left: .36rem;
}
.search .search_logo {
  width: .25rem;
  height: .23rem;
  position: absolute;
  background-image: url("../images/img/search.png");
  background-size: cover;
  background-repeat: no-repeat;
  right: 1.35rem;
  top: .65rem;
}
.search .search_font {
  position: absolute;
  color: #41a5fa;
  font-size: .22rem;
  right: .72rem;
  top: .6rem;
}
.nothing {
  width: 100%;
  height: 8.69rem;
  position: absolute;
  top: 2.1rem;
  left: 0;
}
.nothing img {
  width: 100%;
  height: 8.69rem;
}
.playDiv {
  width: 100%;
  height: 1.64rem;
  border-bottom: .01rem solid #e1e1e1;
  position: relative;
}
.playDiv img {
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  position: absolute;
  left: .41rem;
  top: .46rem;
}
.playDiv .pname {
  color: #000000;
  position: absolute;
  font-weight: bold;
  font-size: .29rem;
  left: 1.77rem;
  top: .56rem;
}
.playDiv .area {
  color: #666666;
  position: absolute;
  font-size: .22rem;
  left: 1.77rem;
  top: 1.11rem;
}
.playDiv .grade {
  color: #666666;
  position: absolute;
  font-size: .22rem;
  left: 3rem;
  top: 1.11rem;
}



