
.header {
  width: 100%;
  height: .8rem;
  background-color: #40a6fb;
  position: fixed;
  top: 0;
  z-index: 999;
}
.header .return {
  position: absolute;
  width: .18rem;
  height: .34rem;
  left: .33rem;
  top: .26rem;
  background-image: url("../images/img/u31.png");
  background-size: cover;
  background-repeat: no-repeat;
}
.header .header_title {
  color: #ffffff;
  font-size: .34rem;
  text-align: center;
  line-height: .8rem;
}
.footer {
  width: 100%;
  height: .98rem;
  background-color: #fafafa;
  position: fixed;
  bottom: 0;
  border-top: .12rem solid #eaeaea;
  border-image: -webkit-linear-gradient(#fafafa, #eaeaea) 30 30;
  z-index: 999;
}
.footer .page {
  width: 1.875rem;
  height: .98rem;
  float: left;
  position: relative;
}
.footer .page a {
  display: block;
  height: 100%;
}
.footer .page .news_bg {
  width: .42rem;
